import React from 'react'
import './index.scss'
import {entries} from "reducer";
import {museumActions} from "action";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {VsonBanner, BackHome} from 'components'
import {Flex} from 'antd-mobile'

interface P {
    className?: string,
    museum: any,
    match: any,
    getMuseumDetail: any
}

interface S {
}

class MuseumIntroduce extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                className = '',
                museum
            },
            state: {}
        } = this
        return (
            <div className={`museum_introduce_page`}>
                <VsonBanner className={'introduce_banner'} options={museum.imgs} />
                <Flex direction={"column"} className={`introduce`}>
                    <div className={'infos'}>
                        <div className={'name'}>{museum.name}</div>
                        <div className={'info'}>
                            <img src="/lsc_h5_hash/imgs/biaoqian@3x.png" alt="" />
                            <span>{museum.describe}</span>
                        </div>
                        <div className={'info'}>
                            <img src="/lsc_h5_hash/imgs/dingwei@3x.png" alt="" />
                            <span>{museum.site}</span>
                        </div>
                        <div className={'info phone bd_d'}>
                            <img src="/lsc_h5_hash/imgs/dianhua@3x.png" alt="" />
                            <span>{museum.phone}</span>
                        </div>
                    </div>
                    <Flex.Item className={'synopsis'}>{museum.synopsis}</Flex.Item>
                </Flex>
                <BackHome />
            </div>
        );
    }

    getDetail = async () => {
        const {match: {params: {id}}, getMuseumDetail} = this.props
        const museum = await getMuseumDetail(id)
        document.title = museum.name
    }

    componentDidMount(): void {
        this.getDetail()
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

function mapStateToProps(state: entries) {
    const {museum} = state.museumReducer
    return {
        museum
    }
}

function mapDispatchToProps(dispatch: any) {
    const {getMuseumDetail} = museumActions
    return {
        ...bindActionCreators({getMuseumDetail}, dispatch),
        dispatch
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(MuseumIntroduce)
